import { ClientResponseError } from "@/ClientResponseError";
import { BaseService } from "@/services/BaseService";
import { ListResult, LogModel } from "@/tools/dtos";
import { CommonOptions, ListOptions, LogStatsOptions } from "@/tools/options";

export interface HourlyStats {
    total: number;
    date: string;
}

export class LogService extends BaseService {
    /**
     * Returns paginated logs list.
     *
     * @throws {ClientResponseError}
     */
    getList(page = 1, perPage = 30, options?: ListOptions): ListResult<LogModel> {
        options = Object.assign({ method: "GET" }, options);

        options.query = Object.assign(
            {
                page: page,
                perPage: perPage,
            },
            options.query,
        );

        return this.client.send("/api/logs", options);
    }

    /**
     * Returns a single log by its id.
     *
     * If `id` is empty it will throw a 404 error.
     *
     * @throws {ClientResponseError}
     */
    getOne(id: string, options?: CommonOptions): LogModel {
        if (!id) {
            throw new ClientResponseError({
                url: this.client.buildURL("/api/logs/"),
                status: 404,
                response: {
                    code: 404,
                    message: "Missing required log id.",
                    data: {},
                },
            });
        }

        options = Object.assign(
            {
                method: "GET",
            },
            options,
        );

        return this.client.send("/api/logs/" + encodeURIComponent(id), options);
    }

    /**
     * Returns logs statistics.
     *
     * @throws {ClientResponseError}
     */
    getStats(options?: LogStatsOptions): Array<HourlyStats> {
        options = Object.assign(
            {
                method: "GET",
            },
            options,
        );

        return this.client.send("/api/logs/stats", options);
    }
}
